import { Cloudinary } from "@cloudinary/url-gen";
import { responsive, HtmlImageLayer } from "@cloudinary/html";

// Initialize Cloudinary
const cld = new Cloudinary({
  cloud: {
    cloudName: 'dg8ibuag5' // Your cloud name
  },
  url: {
    secure: true // Ensure HTTPS URLs
  }
});

// Function to make an image responsive
function makeImageResponsive(elementId, publicId) {
  const imgElement = document.getElementById(elementId);
  if (!imgElement) {
    console.warn(`[Responsive Cloudinary] Element with ID "${elementId}" not found.`);
    return;
  }
  if (!publicId) {
      console.warn(`[Responsive Cloudinary] Missing public ID for element "${elementId}". Skipping.`);
      return;
  }

  console.log(`[Responsive Cloudinary] Applying to: ${elementId} (Public ID: ${publicId})`);
  const cldImage = cld.image(publicId);

  // Apply the responsive plugin using HtmlImageLayer
  try {
      new HtmlImageLayer(imgElement, cldImage, [responsive({
          // You can optionally add steps here, e.g., steps: [800, 1000, 1400]
          // By default, it uses 100px increments.
      })]);
      // Clear the src attribute initially, HtmlImageLayer will populate it.
      imgElement.setAttribute('src', ''); 
  } catch (error) {
      console.error(`[Responsive Cloudinary] Error applying to ${elementId}:`, error);
      // Optionally set a fallback src if initialization fails
      // imgElement.src = cldImage.delivery(format('auto'), quality('auto')).toURL();
  }
}

// Wait for the DOM to be fully loaded
document.addEventListener('DOMContentLoaded', () => {
  console.log('[Responsive Cloudinary] DOM loaded, applying responsive logic...');

  // Define the images to make responsive [elementId, publicId]
  // IMPORTANT: Replace placeholder public IDs if necessary
  const imagesToProcess = [
    ['testimonials-collage', 'uvlizer/cnew1'], // Placeholder - Update if needed
    ['wipe-out-image', 'uvlizer_image_sypnta'],
    ['uvo254-bulb-image', 'uvlizer/device-main'], // Placeholder - Update if needed
    ['benefits-testimonials', 'uvlizer/cifndpuaczptwfatszrb'],
    ['money-back-seal', 'moneybaklogo_-_Edited_optuev'],
    ['happy-customers-desktop', 'uvlizer/qud6qkco87yohoufcfhr'],
    ['happy-customers-mobile', 'uvlizer/kkahzqm8nl8q6mannwsi']
  ];

  // Process each image
  imagesToProcess.forEach(([elementId, publicId]) => {
    makeImageResponsive(elementId, publicId);
  });

  console.log('[Responsive Cloudinary] Finished applying responsive logic.');
}); 